<template>
  <div>
    <v-back></v-back>
    <v-search></v-search>
    <div>
      <h4>电影详情</h4>
      <p>名称：{{detail.name}}</p>
      <p v-if="detail.price">价格：{{detail.price | priceFilter}}</p>
      <p>时间：{{detail.time | timeFilter}}</p>
      <img :src="detail.img">
    </div>
  </div>
</template>
<script>
import {movieDetail} from "../util/request"
  export default {
    data() {
      return{
        detail:{}
      }
    },
    methods: {},
    watch: {},
    mounted() {
      console.log(this.$route.query.id);
      movieDetail(this.$route.query.id).then(res=>{
        console.log(res);
        this.detail = res.data.detail
      })
    }
  }

</script>
<style scoped>
img{
  width: 80%;
}
</style>



